export default "import { Modal, Button } from 'antd';\nimport NiceModal, { useModal } from '@ebay/nice-modal-react';\n\nexport const MyAntdModal = NiceModal.create(({ name }) => {\n  const modal = useModal();\n  return (\n    <Modal\n      title=\"Hello Antd\"\n      visible={modal.visible}\n      onOk={modal.hide}\n      onCancel={modal.hide}\n      afterClose={modal.remove}\n    >\n      Greetings: {name}!\n    </Modal>\n  );\n});\n\nexport default function AntdSample() {\n  return (\n    <>\n      <Button type=\"primary\" onClick={() => NiceModal.show('my-antd-modal', { name: 'Nate' })}>\n        Show Modal\n      </Button>\n      <MyAntdModal id=\"my-antd-modal\" />\n    </>\n  );\n}\n";